@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@300;400;700;900&display=swap');

@import "variables";

body {
  margin: 0;
  font-family: 'Montserrat', sans-serif;
  font-size: 18px;
  line-height: 1.75;
  color: $wht-3;
}

* {
  box-sizing: border-box;
}

ul {
  list-style-type: none;
}

h1 {
  color: $blk-1;
}

h1, h2, h3, h4 {
  margin: 0;
  line-height: 1.25;
}

.side-links {
  position: absolute;
  left: 0;
  top: 45%;
  display: flex;
  flex-direction: column;

  svg {
    fill: $shade-1;
    width: 2rem;
    margin-left: 0.25rem;
  }
}

@import "buttons";
@import "header";
@import "sections";
@import "footer";
@import "queries";
