@import "variables";

@media screen and (min-width: 1023px) {
  body {
    --max-content-width: 930px;
  }
  .hide-pc {
    display: none !important;
  }

  .intro-img {
    bottom: -5%;
    right: 5%;
    transform: scale(0.8);
  }

  .about-img {
    left: -15%;
  }

  .contact-img {
    bottom: 5%;
    transform: scale(1.2);
  }

}

@media screen and (max-width: 1023px) and (min-width: 640px) {
  body {
    --max-content-width: 600px;
  }

  .hide-tablet {
    display: none !important;
  }

  section {
    img {
      width: 100%;
      height: auto;
    }

    img.img-vert {
      height: 100%;
      width: auto;
    }
  }

  section.experience {
    .pane__top {
      button {
        margin: 0.25rem;
      }
    }
  }

  section.skills {

    .pane__middle {
      margin: 0;
      width: 100%;
      flex-direction: column;
      justify-content: flex-start;
      grid-gap: 0;
    }

    .pane__sub {
      flex: none;
    }

    .skill-category {
      flex: 0;
      width: 100%;

      .title-container {
        border-top: 1px solid $blk-3;
      }
    }

    ul.opened {
      border-top: 1px solid $blk-3;
    }
  }

  section.portfolio {
    .portfolio-card {
      &__image {
        flex: 1;
      }

      &__description {
        display: none;
      }
    }
  }

  section.contact {
    .pane__left {
      grid-gap: 20px;

      .section-buttons {
        > :not(:first-child) {
          display: none;
        }
      }
    }
  }

  .intro-img {
    transform: scale(0.8);
    bottom: -5%;
  }

  .about-img {
    left: 50%;
    bottom: auto;
    transform: scale(0.8) translate(-50%, -30%);
  }

  .contact-img {
    bottom: 5%;
    transform: scale(1.2);
  }
}

@media screen and (max-width: 710px) {
  .side-links {
    top: 15%;
    flex-direction: row;
  }
}

@media screen and (max-width: 640px) {
  body {
    --max-content-width: calc(100% - 0.5rem);
  }

  .side-links {
    top: 5%;
  }

  .hide-phone {
    display: none !important;
  }

  section {
    padding-top: 0;
    padding-bottom: 70px;
    flex-direction: column;

    .section-header {
      padding: 0;
    }

    .img-content {
      width: 55%;
      flex: 0.8;

      &.left-side, &.right-side {
        justify-content: center;
        align-items: center;
      }

      img {
        width: 100%;
        height: auto;
      }
    }

    button, .button {
      align-self: center;
      width: 100%;
    }
  }

  section.intro {
    .pane__middle {
      flex-direction: column-reverse;
      grid-gap: 0;
    }

    .pane__right {
      align-items: center;
    }

    .img-wrapper {
      width: 50%;
    }
  }

  section.portfolio {
    max-width: none;

    .pane__middle {
      min-height: 35vh;
    }

    .portfolio-card {
      border-radius: 0;
      box-shadow: none;

      &__image {
        flex: 1;
      }

      &__description {
        display: none;
      }
    }
  }

  section.about {

    .img-content {
      mask-position: center;
    }

    ul > li {
      font-size: 0.9rem;
      padding: 0.5rem;
    }
  }

  section.experience {
    .pane__top {
      button {
        margin: 0.25rem;

        .text {
          display: none;
        }

        .icon {
          margin: 0;
          display: flex;
          align-items: center;

          svg {
            position: relative;
            transform: none;
            right: 0;
          }
        }
      }
    }
  }

  section.skills {

    .pane__middle {
      margin: 0;
      width: 100%;
      flex-direction: column;
      justify-content: flex-start;
      grid-gap: 0;
    }

    .pane__sub {
      flex: none;
    }

    .skill-category {
      flex: 0;
      width: 100%;

      .title-container {
        border-top: 1px solid $blk-3;
      }
    }

    ul.opened {
      border-top: 1px solid $blk-3;
    }
  }

  section.portfolio {
    max-width: none;

    .pane__middle {
      min-height: 35vh;
    }

    .portfolio-card {
      border-radius: 0;
      box-shadow: none;
      height: 300px;

      &__image {
        max-width: 50%;
        height: inherit;
      }

      &__description {
        display: none;
      }
    }
  }

  section.contact {
    max-width: none;

    .pane__middle {
      border-radius: 0;
    }

    .pane__left {
      grid-gap: 20px;

      .section-buttons {
        > :not(:first-child) {
          display: none;
        }
      }
    }
  }

  footer {
    margin-bottom: 70px;

    .top-side {
      flex-direction: column;
    }
  }

  .intro-img {
    transform: scale(0.8) translateY(-50%);
    top: 50%;
    right: auto;
  }

  .about-img {
    left: auto;
    bottom: auto;
    transform: scale(1.4) translateY(-30%);
  }

  .contact-img {
    bottom: 5%;
    transform: scale(1.2);
  }
}
