@import "variables";

a.button, button {
  max-width: 220px;
  padding: 1rem 1.25rem;
  border-radius: 0.5rem;
  outline: none;
  border: none;
  font-weight: bold;
  font-size: 18px;
  font-family: 'Montserrat', sans-serif;
  transition: 0.2s;
  cursor: pointer;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  text-decoration: none;

  &.primary {
    background-color: $shade-5;
    color: $wht-1;
    fill: $wht-1;

    &:hover {
      background-color: $shade-2;
    }
  }

  &.secondary {
    background-color: $wht-1;
    color: $shade-5;
    fill: $shade-5;

    &:hover {
      background-color: $wht-1;
      color: $wht-3;
    }
  }

  &.icon-button {
    width: 70px;
    height: 70px;
  }

  &.icon-side-left {
    svg {
      left: 1rem;
    }
  }

  &.icon-side-right {
    svg {
      right: 1rem;
    }
  }

  .icon {
    margin-left: 1.5rem;

    svg {
      width: 1.5rem;
      height: 1.5rem;
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
    }
  }
}

//
// BURGER BTN
//

.burger-btn {
  background-color: transparent;
  width: 70px;
  height: 70px;
  padding: 0;

  > svg {
    width: 1.5rem;
    height: 1.5rem;
    position: absolute;
    top: 22.5px;
    left: 22.5px;
    transition: 0.1s;
  }

  &.is-menu-open {
    > svg:nth-child(1) {
      top: 33px;
      left: 9px;
      transform: rotate(-20deg);
    }

    > svg:nth-child(2) {
      top: 25px;
      left: 35px;
      transform: rotate(20deg);
    }
  }

}

@keyframes shake-burger-1 {
  0% {
    transform: rotate(0deg);
  }
  25% {
    transform: rotate(-8deg);
  }
  75% {
    transform: rotate(8deg);
  }
  100% {
    transform: rotate(0deg);
  }
}

@keyframes shake-burger-2 {
  0% {
    transform: rotate(0deg);
  }
  25% {
    transform: rotate(2deg);
  }
  75% {
    transform: rotate(-12deg);
  }
  100% {
    transform: rotate(0deg);
  }
}

@keyframes shake-burger-3 {
  0% {
    transform: rotate(0deg);
  }
  25% {
    transform: rotate(-2deg);
  }
  75% {
    transform: rotate(22deg);
  }
  100% {
    transform: rotate(0deg);
  }
}
