@import "variables";

section {
  max-width: var(--max-content-width);
  margin: auto;
  display: flex;
  flex-direction: column;

  .section-header {
    display: flex;
    align-items: center;
    flex-direction: column;
    padding-top: 30px;
    margin-bottom: 2rem;
    position: relative;
    height: 60px;

    .section-nav {
      position: absolute;
      top: 0;
    }

    > h1, > h4 {
      text-align: center;
    }

    .section-title {
      font-weight: 900;
      -webkit-text-stroke: 1px $blk-2;
      -webkit-background-clip: text;
      -webkit-text-fill-color: transparent;
      background-size: 100% 200%;
      transition: 0.5s;
      background-position: 0 -100%;
      background-image: linear-gradient(0deg, #fff 50%, transparent 50%), linear-gradient(90deg, var(--startColor) 2%, var(--endColor) 80%);

      &.active {
        background-position: 0 0;
      }
    }
  }

  .pane {

    &__middle {
      display: flex;
      flex: 1;
      margin: 3rem 0;
      grid-gap: 3rem;
      position: relative;
    }

    &__top {
      display: flex;
      justify-content: space-evenly;
      align-items: center;
      height: 70px;
    }

    &__bottom {
      display: flex;
      height: 70px;
    }

    &__sub {
      flex: 1;
      display: flex;
      flex-direction: column;
    }

    &__left, &__right {
      flex: 1;
      height: 100%;
      display: flex;
      flex-direction: column;
      justify-content: space-evenly;

      .img-wrapper {
        width: 100%;
        aspect-ratio: 1;
        overflow: hidden;
        position: relative;
        mask-repeat: no-repeat;
      }

      img {
        position: absolute;
        width: 100%;
        height: auto;
      }
    }
  }
}

section.intro {
  height: 100vh;

  .pane__right .img-wrapper {
    mask-image: url('../assets/blob1.svg');
    background: linear-gradient(60deg, $shade-4, $shade-9);
  }
}

section.about {
  .pane__left {
    mask-image: url('../assets/blob2.svg');
    mask-repeat: no-repeat;
    background: linear-gradient(220deg, $shade-1, $shade-5);
    overflow: hidden;
  }

  .summary-item {
    flex: 1;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    padding: 0 1rem;
  }
}

section.skills {

  .skill-category {
    flex: 1;

    .title-container {
      padding: 0.5rem;
      line-height: 1;
      cursor: pointer;
      position: relative;

      > h3 {
        color: $blk-3;
      }

      .arrow-container {
        position: absolute;
        right: 0.5rem;
        top: 25%;
        width: 1.5rem;
        height: 1.5rem;

        svg {
          width: 100%;
          height: 100%;
        }
      }
    }
  }

  ul {
    height: 0;
    overflow: hidden;
    transition: 0.2s;
    padding: 0 1rem;
    margin: 0;

    &.opened {
      height: var(--opened-height);
    }
  }

  li {
    margin-bottom: 0.5rem;

    &:last-child {
      margin-bottom: 0;
    }

    > .skill-name {
      display: flex;
      align-items: center;
      justify-content: space-between;

      > .name {
        color: $blk-3;
      }
    }

    > .progress-bar {
      background-color: $wht-2;
      height: 0.5rem;
      border-radius: 2rem;
      overflow: hidden;

      > .progress-bar-fill {
        background-color: $shade-2;
        height: 100%;
      }
    }
  }
}

section.experience {
  .pane__middle {
    flex-direction: column;
    align-items: center;

    .exp-item {
      width: 80%;
    }
  }
}

section.portfolio {
  .number-dot {
    width: 3rem;
    height: 3rem;
    background-color: $shade-1;
    border-radius: 50%;
    font-weight: 900;
    -webkit-text-stroke: 1px $blk-2;
    -webkit-text-fill-color: white;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 2rem;
    cursor: pointer;
    transition: 0.2s;

    &:hover {
      background-color: $shade-3;
    }

    &.is-active {
      background-color: $shade-0;
    }
  }

  .portfolio-card {
    position: absolute;
    width: 100%;
    height: 100%;
    opacity: 0;
    transition: 0.2s;
    display: flex;
    grid-gap: 2rem;
    max-height: 400px;
    padding-right: 1rem;
    overflow: hidden;
    box-shadow: 0 0 2px 2px #00000080;
    border-radius: 1rem;
    pointer-events: none;

    &.visible {
      opacity: 1;
      pointer-events: all;
    }

    &__info {
      display: flex;
      flex-direction: column;
      justify-content: space-evenly;
      flex: 1;

      a {
        width: 100%;
        max-width: none;
      }
    }

    &__image {
      flex: 2;
      --max-img-height: 400px;
      display: flex;
      align-items: center;
      justify-content: center;
      max-height: var(--max-img-height);
      height: 400px;
      padding: 10px 0;
      overflow: hidden;
      background: radial-gradient(circle at 0 50%, #7100d3 20%, transparent 95%);

      a {
        height: 100%;
        width: 100%;
        position: relative;
      }

      img {
        position: absolute;
      }
    }
  }

  .pane__middle {
    min-height: 50vh;
  }
}

section.contact {

  .pane__middle {
    display: flex;
    align-items: center;
    width: 100%;
    padding: 2rem;
    border-radius: 1rem;
    background: linear-gradient(30deg, $shade-2, $shade-8);
    color: white;
  }

  .section-buttons {
    height: 60px;
    display: flex;

    svg {
      height: 100%;
      width: auto;
      margin-left: 0.5rem;
      fill: white;
    }
  }

  .pane__right .img-wrapper {
    mask-image: url('../assets/blob3.svg');
    background-color: #00000050;
  }
}
