@import "variables";

footer {
  background: linear-gradient(45deg, $shade-1, $shade-7);
  color: white;
  padding: 2rem 0;
  width: 100%;

  > .footer-contents {
    width: 90%;
    max-width: var(--max-content-width);
    height: 100%;
    margin: auto;
    display: flex;
    flex-direction: column;

    > .content {
      flex: 1;
      display: flex;
      align-items: center;

      &.top-side {
        justify-content: space-evenly;
      }

      &.bottom-side {
        justify-content: center;
        opacity: 0.5;

        img {
          height: 1.5rem;
        }
      }
    }
  }

  img, svg {
    height: 2.5rem;
    width: auto;
    margin: 0.5rem;
    fill: white;
  }
}
