@import "variables";

header {
  position: sticky;
  top: 0;
  height: 70px;
  width: 100%;
  background-color: white;
  font-weight: bold;
  z-index: 3;

  > .header-contents {
    width: 100%;
    max-width: var(--max-content-width);
    height: 100%;
    margin: auto;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }

  .header-title-buttons {
    position: absolute;
    right: 0;
    top: 0;
    display: flex;
  }
}

header .nav-buttons {
  background-color: white;
  display: flex;
  height: 70px;
  flex: 3;

  .header-button {
    color: $blk-1;
    flex: 1;
    max-width: 100%;

    &:hover {
      color: $shade-3;
    }
  }

  .rainbow-border-container {
    width: 100%;
    height: 4px;
    position: absolute;
    top: 0;
    display: flex;

    .pretend-button-border {
      height: 100%;
      flex: 1;
      background: linear-gradient(90deg, var(--border-color-start), var(--border-color-end));
    }
  }
}

.header-button {
  background-image: linear-gradient(180deg, white 50%, transparent 50%), linear-gradient(90deg, var(--border-color-start), var(--border-color-end));
  background-size: 100% 200%;
  background-position: 0 0;
  height: 70px;
  padding: 0;
  border-radius: 0;
  position: relative;

  &:hover .button-border {
    height: 10px;
  }

  &.active {
    background-position: 0 100%;

    > .text {
      color: white;
    }
  }

  .button-border {
    margin: 0;
    position: absolute;
    width: 100%;
    height: 4px;
    background: linear-gradient(90deg, var(--border-color-start), var(--border-color-end));
    bottom: 0;
    transition: 0.2s;
  }
}

.header-backdrop {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  background-color: #00000040;
  z-index: 2;
}

.name-btn.active {
  background-position: 0 0;

  > .text {
    color: black;
  }
}

@media screen and (min-width: 1023px) {
  header {
    box-shadow: 0 3px 3px 0 #00000050;

    .header-title {
      flex: 1;

      .name-btn {
        max-width: none;
        width: 100%;
        padding: 1rem;
      }
    }
  }

  .header-button {
    width: 140px;
  }
}

@media screen and (max-width: 1023px) and (min-width: 640px) {
  header {

    .header-title {
      display: flex;
      justify-content: center;
      width: 100%;
    }

    .header-contents {
      flex-direction: column;

      .nav-buttons {
        margin-left: 0;
        width: 100%;
      }
    }

    .name-btn.active {
      background-position: 0 0;

      > .text {
        color: black;
      }
    }

    .header-title .button-border {
      display: none;
    }

    .nav-buttons {
      align-items: flex-end;
      position: absolute;
      top: 70px;
      height: 4px;
      overflow: hidden;
      transition: height 0.2s;
      box-shadow: 0 3px 3px 0 #00000050;

      &.display-header {
        height: 70px;
      }
    }
  }
}

@media screen and (max-width: 640px) {
  header {
    position: fixed;
    top: auto;
    bottom: 0;

    .header-contents {
      max-width: none;
    }

    .header-title {
      display: flex;
      justify-content: space-between;
      width: 100%;
      > button {
        padding-left: 22px;
      }
    }

    .rainbow-border-container {
      z-index: 1;
    }

    .header-title .button-border {
      display: none;
    }

    .name-btn.active {
      background-position: 0 0;

      > .text {
        color: black;
      }
    }

    .nav-buttons {
      position: absolute;
      top: 0;
      box-shadow: 0 -3px 3px 0 #00000050;
      max-width: none;
      width: 100%;
      height: 4px;
      overflow: hidden;
      transition: top 0.2s, height 0.2s;
      flex-wrap: wrap;

      &.display-header {
        top: -140px;
        height: 140px;
        border-bottom: 1px solid $blk-3;
      }

      .header-button {
        min-width: 30%;
        font-size: 1rem;

        .button-border {
          bottom: auto;
          top: 0;
        }

        &.active .button-border {
          top: 2px;
          bottom: auto;
        }
      }
    }
  }
}
